// console.log('123', process.env)
// 1.服务器根目录，可以新建环境变量配置文件（文件名固定）
// 2.脚手架环境webpack内置好的，文件名（可以修改的但是要改配置--自行百度）
// .env.development
// .env.production
// 3.环境变量文件中，定义变量名NODE_ENV(固定)，BASE_URL(固定)，自定义变量名VUE_APP_开头（规定）
// key名必须一致，写代码一套代码.key名，会自动匹配环境变量值
// 4.yarn serve 启动项目，.env.development 内的变量挂载到process.env属性上
// yarn build打包项目，.env.production 内的变量挂载到process.env属性上

if (process.env.NODE_ENV === 'production') {
  console.log = function () { }// 覆盖所有的打印语句
  console.warning = function () { }
  console.dir = function () { }
  console.error = function () { }
}
