import '@/utils/console.js'// 去掉打印语句
import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import 'highlight.js/styles/default.css' // 代码高亮的样式
import 'amfe-flexible'// 设置标签体大小（移动端适配）
import directiveObj from './utils/directive'
import '@/VueComponent.js'// vant组件注册，单独分离成一个js文件，让main.js更清晰

Vue.config.productionTip = false

// 执行目标对象里的install方法并传入Vue类，use方法内会调用install
Vue.use(directiveObj)

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')

// 知识点讲解
// 模拟后端返回的JSON字符串响应数据
const str = '[{"id":13303003901919199999},{"id":1398803003901919199999},{"id":18703003901919199999}]'
const arr = JSON.parse(str)// 数字
console.log(arr)
// 问题：转成数组类型，数字发现精度丢失（后四位不准确）
// 问题2：如果把丢失精度的文章的id传给后台，后台是找不到对应文章的
// 原因1：后端id生成算法，和前端不同，产生19位数字
// 原因2：js有效安全数字是有范围的
// Number.MAX_SAFE_INTEGER-->最大安全数字范围 9007199254740991
// Number.MIN_SAFE_INTEGER-->最小安全数字范围

// 解决：json-bigint第三方包
// 内部，把数字JSON字符串拆分，分成对象的形式/字符串形式
// 安装： npm i json-bigint
// 两种引入：
// console.log(jsonB);
// import jsonB from 'json-bigint'
// ({ storeAsString: true })是否以字符串的形式存储，就是转换时以字符串存储，不要BigNumber对象
const jsonBig = require('json-bigint')({ storeAsString: true })
// webpack+node.js环境执行main.js，所以在这里是可以使用require，和import导入的东西一样
console.log(jsonBig.parse(str))// BigNumber自定义类的对象
